import React, { FC } from 'react';

import Text from 'components/Text';
import Img from 'components/Img';
import { ListItem, UnorderedList } from 'components/List';
import Chapter from 'components/Chapter';
import Gallery from 'components/Gallery';
import Link from 'components/Link';

import { ChapterType } from 'pages/Chapters';
import { GALLERY } from 'pages/Sources';

import { MapChapters } from 'utils';

import { ReactComponent as JumpingRope } from 'assets/jumping-rope.svg';

const metadata = {
	id: 'vestibularni-vnimani',
	icon: JumpingRope,
	title: 'Vestibulrn vnmn',
	color: '#5c5791',
	subchapters: MapChapters('vestibularni-vnimani', 'podkapitola', [
		'Hypersenzitivita',
		'Hyposenzitiva',
		'Vyhledvaj extra podnty',
		'Slab pohybov diskriminace',
	]),
	activities: MapChapters('vestibularni-vnimani', 'aktivita', [
		'Houpn',
		'Houpac most',
		'Houpac prkno',
		'Skkn',
		'Zaven hlavou dol',
		'Jzda na pojzdn desce',
		'Oten na twisteru',
		'Lezen do vych mst',
	]),
} as ChapterType;

const Content: FC = () => (
	<Chapter variant="main" {...metadata}>
		<Text>
			Vestibulrn systm zajiuje informace o gravitaci, rovnovze a pohybu, o
			poloze na hlavy a tla ve vztahu k povrchu zem. Pomh nm generovat
			svalov tonus, pomhajc pohybovat se hladce a efektivn. k nm jestli
			jsme to my, kdo jsme v pohybu, i vci okolo ns. Je dleit ve vvoji
			ei, zejmna pro rozvoj, zachycen a porozumn slyenmu. Proto mnoh
			dt, kter maj pote ve vestibulrnm vnmn, se v ei opouj.
		</Text>

		<Chapter {...metadata.subchapters[0]}>
			<Text>Meme poznat dt podle toho, e:</Text>
			<UnorderedList>
				<ListItem>bude reagovat pecitlivle na bn pohyb</ListItem>
				<ListItem>
					vyhb se bhn, jzd na kole, skovn a tanen
				</ListItem>
				<ListItem>
					bude se vyhbat houpakm, klouzakm a kolotom a je celkov velmi
					opatrn
				</ListItem>
				<ListItem>nechce pedklnt hlavu, mnit jej pozici</ListItem>
				<ListItem>m problmy pi jzd v aut</ListItem>
				<ListItem>je velmi nejist na schodech</ListItem>
			</UnorderedList>
		</Chapter>

		<Chapter {...metadata.subchapters[1]}>
			<Text>Meme poznat dt podle toho, e:</Text>
			<UnorderedList>
				<ListItem>
					m problm pizpsobit polohu tla pslun aktivit, take je
					neobratn a m zvltn postaven tla
				</ListItem>
				<ListItem>
					nevm si rizik vek a je schopn skoit z velk vky
				</ListItem>
				<ListItem>nevm se, e vc se dala do pohybu</ListItem>
				<ListItem>
					nem motivaci se aktivn hbat, ned ruce ped sebe, kdy pad
				</ListItem>
			</UnorderedList>
		</Chapter>

		<Chapter {...metadata.subchapters[2]}>
			<Text>Meme poznat dt podle toho, e:</Text>
			<UnorderedList>
				<ListItem>
					m velmi rdo pohyb veho druhu, to se dokoleka, nebo se houpe,
					ani by ho postihla zvra nebo nevolnost
				</ListItem>
				<ListItem>
					asto se kve dopedu a dozadu, nebo chod dokoleka a zrove dl
					kolbav pohyby
				</ListItem>
				<ListItem>
					miluje trampolny, skkn z vek a zaven vzhru nohama
				</ListItem>
				<ListItem>
					mv autostimulan projevy, jako tepotn rukou a kmitn prst u
					rukou
				</ListItem>
			</UnorderedList>
		</Chapter>

		<Chapter {...metadata.subchapters[3]}>
			<Text>Meme poznat dt podle toho, e:</Text>
			<UnorderedList>
				<ListItem>asto pad ze idle, pi stn i pi chzi</ListItem>
				<ListItem>
					je dezorientovan pi zmn smru a nedoke rozpoznat, kdy se mu
					udl patn (pozvrac se po dlouhm houpn)
				</ListItem>
				<ListItem>
					m nzk svalov napt - tlo psob jako mkk a slab
				</ListItem>
				<ListItem>
					nevydr dlouho ve vzpmen pozici, asto u bhem cvien/hodiny
					kon poloen s hlavou na stole
				</ListItem>
				<ListItem>asto je brzy unaven</ListItem>
				<ListItem>
					m problm s koordinac pohyb obou polovin tla, s hrubou a jemnou
					motorikou
				</ListItem>
				<ListItem>nem urenou dominantn ruku</ListItem>
				<ListItem>
					m pote s naplnovnm pohyb, nev jak m nov pohyb udlat
					(pekroen kamene, peskoen kalue)
				</ListItem>
				<ListItem>
					peskoil fzi lezen, rovnou se z plazen postavil a zaal chodit
				</ListItem>
			</UnorderedList>
		</Chapter>

		<Chapter variant="activity" {...metadata}>
			<Text>
				Je poteba sledovat negativn reakce pi aktivitch, kter
				zprostedkovvaj vjemy a stimuluj systm. Okamit vak musme ukonit
				aktivitu, kdy se dostav nkter z danch projev:
			</Text>

			<UnorderedList>
				<ListItem>Nepravideln dchn</ListItem>
				<ListItem>Zmna barvy v oblieji</ListItem>
				<ListItem>Pocen</ListItem>
				<ListItem>Panika</ListItem>
				<ListItem>Zchvat zkosti</ListItem>
			</UnorderedList>

			<Text>
				Vechny innosti na rozvoj vestibulrnho systmu jsou zaloeny na
				pohybu tla, respektive hlavy. Tak, aby se rovnovn vnmn ve
				vnitnm uchu mlo monost, pi svm deficitu, se rozvjet tak, jak
				potebuje.
			</Text>

			<Chapter {...metadata.activities[0]}>
				<Text>
					Houpat se mohou dti klasicky na houpace, ale pokud na ni nemte
					msto, houpat mete dt i v dece, nebo na zavenm lan. Skvl
					jsou houpaky ploch rovn, na kterch si tak mou stimulovat hmat
					(pejd rukama po zemi). Dle se daj vyut jako ncvik koordinace
					pohybu, dt se houpe v houpace (le na bku) a zrove si hz
					balon, nebo kruhy na uren msto.
				</Text>
				<Text>
					Pokud nemete vrtat do stropu, daj se vyut stojany na zaven
					houpaky.
				</Text>
				<Gallery>
					<Img {...GALLERY['chapter-2-houpani1']} />
					<Img {...GALLERY['chapter-2-houpani2']} />
				</Gallery>
			</Chapter>

			<Chapter {...metadata.activities[1]}>
				<Text>
					Most mete vyut jak na houpn, tak pi oton, jako malu prlezku.
					Trnujete tm rovnovn ctn, hrubou motoriku a vestibulrn
					systm.
				</Text>
				<Gallery>
					<Img {...GALLERY['chapter-2-most1']} />
				</Gallery>
			</Chapter>

			<Chapter {...metadata.activities[2]}>
				<Text>
					Ureno pro dti s vyvinutjmi motorickmi schopnostmi. Prkno se
					skvl hod k vvoji rovnovhy, tonusu tla, stability a hrub
					motoriky.
				</Text>
				<Gallery>
					<Img {...GALLERY['chapter-2-prkno1']} />
				</Gallery>
			</Chapter>

			<Chapter {...metadata.activities[3]}>
				<Text>
					Dalm dleitm pohybem pro rozvoj vestibulrnho vnmn je skkn.
					Vhodnou a dostupnou pomckou je gymnastick m, nebo trampolna. Na
					balonu je dobr, aby dt skkalo ve stoje, za pomoci dren rodie.
					Baln bu postavte do rohu mstnosti, nebo o hranu pohovky, do kter
					pot me dt i padat. Dobr je skkn doplnit psnikou, nebo
					kankou pro podporu rytmiky. Dle me skkat do vech smr, pokud
					u zvld skkat samo, nechme ho.
				</Text>
				<Text>
					Pokud nemte trampolnu, vyut mete i na sebe poskldan polte,
					nebo molitany.
				</Text>
			</Chapter>

			<Chapter {...metadata.activities[4]}>
				<Text>
					Pokud jste si vimli, e se vae dt rdo houpe hlavou dol, nechte
					ho. Dopluje si tak vestibulrn input, kter mu chyb. Houpat se me
					samo, poloenm na postel, nebo ho mete vzt za nohy a houpat hlavou
					dol. Provdjte tuto aktivtu, ale pouze po krtkou dobu, aby se
					nepekrvila hlava.
				</Text>
			</Chapter>

			<Chapter {...metadata.activities[5]}>
				<Text>
					Vyut mete skateboardu, nebo i sthovac desku na kolekch. Vhoda
					je, kdy jde pivzat na provzek. Pro men a mn obratn dti,
					kter se neum odret je tohle monost, jak se mohou povozit. Lehnout
					si me dt na bicho, nebo sedt. Pipravit pro n mete
					pekkovou drhu, nebo vozit k odmn, i to vyut jak msto k
					dalm innostem. Nkterm dtem se pi pedklonu hlavy lpe pracuje,
					jsou vnmavj (zapoj vce vestibulrn vnmn), mete jim
					nachystat puzzle na skldn, malovn, nebo jin innosti.
				</Text>
				<Gallery>
					<Img {...GALLERY['chapter-2-deska1']} />
					<Img {...GALLERY['chapter-2-deska2']} />
				</Gallery>
				<Text>
					Inspiraci pro dal aktivity s deskou naleznete{' '}
					<Link
						href="https://www.mymundaneandmiraculouslife.com/scooter-board-sensory-activities/"
						target="_blank"
						rel="noreferrer"
						underline
					>
						zde
					</Link>
					.
				</Text>
			</Chapter>

			<Chapter {...metadata.activities[6]}>
				<Text>
					Sta podit obyejn twister a dt se ot samo, nebo ho chytte
					za ruce a otte ho vy.
				</Text>
			</Chapter>

			<Chapter {...metadata.activities[7]}>
				<Text>
					Pokud jste si vimli, e dt rdo leze na nbytek, stoly a celkov
					vy prostory, uzpsobte mu prosted tak, aby ml potebu nasycenou,
					ale byl v bezpe. Vyut mete ebiny, lezeckou stnu, poschoovou
					postel.
				</Text>
				<Text>
					Pklad instalace ebin v jedn domcnosti: Zde jsou ebiny spojene
					i s houpakou, lanem, ebiny jsou pedsunuty.
				</Text>
				<Gallery>
					<Img {...GALLERY['chapter-2-lezeni1']} />
				</Gallery>
			</Chapter>
		</Chapter>
	</Chapter>
);

export default {
	...metadata,
	Content,
};
