import React, { FC } from 'react';

import Text from 'components/Text';
import Img from 'components/Img';
import { ListItem, UnorderedList } from 'components/List';
import Chapter from 'components/Chapter';
import Gallery from 'components/Gallery';

import { ChapterType } from 'pages/Chapters';
import { GALLERY } from 'pages/Sources';

import { MapChapters } from 'utils';

import { ReactComponent as MuscleUp } from 'assets/muscle-up.svg';

const metadata = {
	id: 'proprioceptivni-vnimani',
	icon: MuscleUp,
	title: 'Proprioceptivn vnmn',
	color: '#688aaf',
	subchapters: MapChapters('proprioceptivni-vnimani', 'podkapitola', [
		'Hypersenzitivita',
		'Hyposenzitiva',
	]),
	activities: MapChapters('proprioceptivni-vnimani', 'aktivita', [
		'Ztov deky/nkrnky/polte',
		'Prolzn tunelem',
		'Hra s lanem',
		'Kopn a bouchn do me/krabice/paprov taky',
		'Obkreslovn tla',
		'Vibrace',
		'Tluen kladivem',
		'Hra s lahvemi',
	]),
} as ChapterType;

const Content: FC = () => (
	<Chapter variant="main" {...metadata}>
		<Text>
			Jinm slovem polohocit, i pohybocit. Je to vnmn,kter dm dv skrze
			lachy a svaly informace o naem pohybu, kde se nachzej rzn sti tla
			ani bychom se nj museli dvat. Propriocepce se podl na uvdomovn si
			vlastnho tla a jeho pohyb. Je nezbytn pro vechny motorick dovednost
			(e, chzi, manipulace s pedmty) koordinaci a motorick plnovn. Tm,
			e ctme hranice svho tla, se ctme v bezpe.
		</Text>

		<Chapter {...metadata.subchapters[0]}>
			<Text>Meme poznat dt podle toho, e:</Text>
			<UnorderedList>
				<ListItem>projevuje se zaujmnm zvltnch pozic</ListItem>
				<ListItem>neikovnm zachzenm s drobnmi pedmty</ListItem>
				<ListItem>
					kdy se potebuje na nco podvat nato se celm tlem (nejen oima)
				</ListItem>
				<ListItem>
					vdom se vyhb novm vzvm  nechce jezdit na kole, vylzt na
					ebk
				</ListItem>
			</UnorderedList>
		</Chapter>

		<Chapter {...metadata.subchapters[1]}>
			<Text>Meme poznat dt podle toho, e:</Text>
			<UnorderedList>
				<ListItem>
					m nzk svalov tonus, psob ochable a asto se opr o nbytek,
					stny
				</ListItem>
				<ListItem>neuvdomuje si polohu svho tla v prostoru</ListItem>
				<ListItem>neuvdomuje si vlastn pocity (hlad, ze)</ListItem>
				<ListItem>nar do objekt a lid, klopt nebo se kolb</ListItem>
				<ListItem>
					m slabou jemnou motoriku, chop m ochabl, padaj mu vci z rukou
				</ListItem>
				<ListItem>
					m pote s e, jezenm a jinm orln  motorickmi pohyby
				</ListItem>
				<ListItem>
					strk si vci do pusy (koue rukvy, tuky, nkdy i sklo a jin velmi
					tvrd materily)
				</ListItem>
			</UnorderedList>
		</Chapter>

		<Chapter variant="activity" {...metadata}>
			<Text>
				U takovch dt je poteba, aby maximln zapojily sv svaly, pohyb
				tla, nebo na tlo byl vyvjen tlak. Tak, aby doopravdy ctily cel sv
				tlo.
			</Text>

			<Chapter {...metadata.activities[0]}>
				<Text>
					Deky plnn sypkm materilem jsou provan, tak aby pi pikryt
					dtte objaly cel tlo. Daj se koupit pmo hotov, uit podle
					velikosti a hmotnosti vaeho dtte, nebo muete vyut t peiny.
					Vhodn pro dti, kter maj problm s usnnm, jsou neklidn,
					potebuj ctit vhu na svm tle. Polt je vhodn pro dt, kter
					maj problm &ldquo;v klidu sedt&ldquo;, vrt se na idli a neum se
					soustedit na koly. Vyrb se v mnoha podobch (delfn, pejsek).
				</Text>
				<Text>
					Nikdy vak ztovky nenechvejte na dtech poloen dlouho, je to pro
					n fyzicky nron.
				</Text>
				<Gallery>
					<Img {...GALLERY['chapter-3-zatezove1']} />
					<Img {...GALLERY['chapter-3-zatezove2']} />
				</Gallery>
			</Chapter>

			<Chapter {...metadata.activities[1]}>
				<Text>
					Zapojen celho tla, dleit je zde prvek plazen v ohranienm
					prostoru. Je si tak jasn vdomo hranic svho tla. Tunely mete
					vyut ltkov, tunely na hitch nebo set z pytl velk tunel a
					nechat ho prolzt otvory.
				</Text>
			</Chapter>

			<Chapter {...metadata.activities[2]}>
				<Text>
					Aby dt dobe ctilo sv svaly a lachy, je dobr ho nechat viset na
					lan, nebo se s lanem petahovat. Uct tak zcela svou slu a zrove
					z toho mete udlat hru na petahovanou. Pokud nem dt partnera na
					petahovn, mete lano zavzat za nohu stolu. Aby mu lano
					neklouzalo, mete mu na nm udlat uzlky a zkombinovat aktivitu s
					pojzdnou deskou (zrove stimulace vestibulrnho vnmn). Lano
					mete tak dt na zem, aby po nm dt chodilo, nebo ho peskakovalo.
				</Text>
				<Gallery>
					<Img {...GALLERY['chapter-3-lano1']} />
				</Gallery>
			</Chapter>

			<Chapter {...metadata.activities[3]}>
				<Text>
					Aktivita na regulaci a registraci vlastn sly. Dt vizuln hned
					vid, jakou slu mus vyvinout pro to, aby se m dostal na uren
					msto, nebo taka roztrhla.
				</Text>
			</Chapter>

			<Chapter {...metadata.activities[4]}>
				<Text>
					Aktivita na poznn hranic svho tla, uren kde v prostoru se
					nachzej nae konetiny. Vyut meme velkou roli papru a
					obkreslen tukou, nsledn si obkresln ast vybarvit a pojmenovat.
					Nebo tlo meme ohraniit hrakami, tstovinami, mkoli, aby tak
					bylo jasn, e tam dt leelo.
				</Text>
			</Chapter>

			<Chapter {...metadata.activities[5]}>
				<Text>
					Men dti meme posadit na suiku, aby ctily vibrace. Nebo pout
					elektrick kartek a pejdt po tla. Pokud m vae dt problmy v
					oblasti oromotoriky (oslaben svaltvo st) mete vibrace pout i
					tam. U citlivjch dt zanejte vdy od jinch st tla, pot
					pejdte k dlanm a pak a vyuijte vibrace v stech, aby si na tento
					pocit zvyklo. Vyut mete jak elektrickch kartk, tak specilnch
					logovibrtor. Dt si tak lpe uvdom jednotliv doteky, jak na
					konetinch, tak v stech.
				</Text>
				<Gallery>
					<Img {...GALLERY['chapter-3-vibrace1']} />
				</Gallery>
			</Chapter>

			<Chapter {...metadata.activities[6]}>
				<Text>
					Dt me zatlouct devn kolky do zem, nebo se daj koupit
					zatloukac dtsk sady. Opt jde o regulaci a registraci vlastn sly.
				</Text>
			</Chapter>

			<Chapter {...metadata.activities[7]}>
				<Text>
					Pouijte ne docela naplnn plastov lhve (naplnit je mete vodou,
					pskem, kulikami, mdlovou vodu), dt m lhve penet, nosit v
					tace, kutlet po zemi atd.
				</Text>
			</Chapter>
		</Chapter>
	</Chapter>
);

export default {
	...metadata,
	Content,
};
