import React, { FC } from 'react';

import Text from 'components/Text';
import { ListItem, UnorderedList } from 'components/List';
import Chapter from 'components/Chapter';
import Img from 'components/Img';
import Gallery from 'components/Gallery';
import Link from 'components/Link';

import { ChapterType } from 'pages/Chapters';
import { GALLERY } from 'pages/Sources';

import { MapChapters } from 'utils';

import { ReactComponent as Hand } from 'assets/hand.svg';

const metadata = {
	id: 'hmatove-vnimani',
	icon: Hand,
	title: 'Hmatov vnmn',
	color: '#548b8e',
	subchapters: MapChapters('proprioceptivni-vnimani', 'podkapitola', [
		'Hypersenzitivita',
		'Hyposenzitiva',
		'Vyhledvaj extra podnty',
		'Slab taktiln diskriminace',
	]),
	activities: MapChapters('proprioceptivni-vnimani', 'aktivita', [
		'Miska s poklady',
		'Patln se v tstu',
		'Hry s ledem',
		'Hry s pskem',
		'Kinetick psek',
		'Roztrn krmu, mdla, peelingu',
		'Hry s pnou',
		'Hmatov pytlky',
		'Balny',
		'Senzorick balny',
		'Pohyb v prod',
		'Plastelna, playdough, play foam',
		'Hry s vodou',
		'Sensory bag',
		'Taktiln chodnk',
		'Makn konek prst',
		'Senzorick taktiln tabule',
		'Kutlen mem po tle',
	]),
} as ChapterType;

const Content: FC = () => (
	<Chapter variant="main" {...metadata}>
		<Chapter {...metadata.subchapters[0]}>
			<Text>Meme poznat tyto dti podle toho, e:</Text>
			<UnorderedList>
				<ListItem>jsou extrmn pecitlivl na rzn hmatov podnty</ListItem>
				<ListItem>
					nerady se objmaj, maj hrzu z dotek, odtahuj se pi hrozb
					kontaktu
				</ListItem>
				<ListItem>
					asto dtem bv nepjemn urit druh materilu obleen tak moc, e
					ho odmtaj nosit
				</ListItem>
				<ListItem>
					pro nkter je dotyk zdrojem nezvladatelnch intenzivnch vjem, kdy i
					sebemen vyvol ataku
				</ListItem>
				<ListItem>
					umyt vlas a sthn neht je pro n extrmn nepjemn
				</ListItem>
				<ListItem>
					preferuj dlouh rukvy a kalhoty a kdy fouk odmtaj jt ven
				</ListItem>
				<ListItem>
					jsou vybrav v jdle  nechtj jst urit potraviny, urit
					struktury (ri, zeleninu, polvku, omky) mou preferovat uritou
					teplotu potravin
				</ListItem>
			</UnorderedList>
		</Chapter>

		<Chapter {...metadata.subchapters[1]}>
			<Text>Meme poznat tyto dti podle toho, e:</Text>
			<UnorderedList>
				<ListItem>
					maj men citlivost na hmatov podnty, chovaj se jako by nectily
					bolest
				</ListItem>
				<ListItem>neuvdomuj si, e je nco pli hork/studen</ListItem>
				<ListItem>nezaznamenaj eznou, i bodnou rnu</ListItem>
				<ListItem>
					fyzicky ubliuj sob, jinm lidem i zvatm, neuvdomuj si svou
					bolesti bolest jinch
				</ListItem>
				<ListItem>nevimnou si, kdy nco upust</ListItem>
			</UnorderedList>
		</Chapter>

		<Chapter {...metadata.subchapters[2]}>
			<Text>Meme poznat tyto dti podle toho, e:</Text>
			<UnorderedList>
				<ListItem>neustle se dotkaj vc a lid</ListItem>
				<ListItem>
					maj rdy, kdy se zapin, u takov aktivity vydr dlouho (hrabn v
					hln, psku)
				</ListItem>
				<ListItem>
					krbou se, kouou se, bhaj naboso, makaj intenzivn rzn vci
				</ListItem>
				<ListItem>vyhledv tlak, zalzaj si pod tk objekty</ListItem>
				<ListItem>
					aktivn nalzaj urit materily  ply, vlnu, vrazn hladk
					povrchy, pot po nich dlouho pejd prsty
				</ListItem>
			</UnorderedList>
		</Chapter>

		<Chapter {...metadata.subchapters[3]}>
			<Text>Meme poznat tyto dti podle toho, e:</Text>
			<UnorderedList>
				<ListItem>
					vypadaj, jako by nemly pod kontrolou vlastn dotek
				</ListItem>
				<ListItem>
					nedotkaj se vc, nebo je nezvedaj tak, jako ostatn
				</ListItem>
				<ListItem>
					nevm si malch detail a textur, mus k tomu zapojit zrak, aby byl
					schopen vci rozliit
				</ListItem>
				<ListItem>
					aby dokzal rozliit, kterou sti tla hbe, mus se na ni dvat
				</ListItem>
				<ListItem>
					radji stoj, ne sed, aby mlo ve vizuln pod kontrolou
				</ListItem>
			</UnorderedList>
		</Chapter>

		<Chapter variant="activity" {...metadata}>
			<Text>
				Aktivity jsou uren pro vechny dti. A u je Vae dt
				hyposenzitivn, nebo hypersenzitivn, podnty potebuje. Pouze muste
				upravit, jakm zpsobem mu aktivitu nabzte. Pokud se tt rznch
				povrch, nebo m problm povrchy vbec zaregistrovat, vnmejte jejich
				reakce a podle nich se ite. Aktivita by mla bt zbava jak pro dt,
				tak pro vs. Jde zejmna o proit pozitivnho zitku s hmatovm
				vnmnm.
			</Text>

			<Chapter {...metadata.activities[0]}>
				<Text>
					Prodn materily jsou nejlep pro rozvoj hmatu. Re, oka, mouka,
					cizrna a dal sypk lutniny jsou skvlm pomocnkem. Mete do
					misek dt kaminky, mule, katany, lego a zahrat si loto, nebo
					vytahovat pedmty poslepu a hadat, co jsou za. Nebo jenom nasypat
					plnou msu re, psku, mouky a nechat je si uvat pjemnho pocitu.
					Ndoba by mla bt co nejvy, aby ruka byla ponoena nejlpe a po
					lokty.
				</Text>
				<Gallery>
					<Img {...GALLERY['chapter-4-miska1']} />
					<Img {...GALLERY['chapter-4-miska2']} />
					<Img {...GALLERY['chapter-4-miska3']} />
				</Gallery>
			</Chapter>

			<Chapter {...metadata.activities[1]}>
				<Text>
					Skvl aktivita i pro dt s potebou silnho stisku. Nechte je mchat
					tsto holma rukama a zat zkuenost s novmi substancemi. Navc
					budou mt dti pocit, e jsou uiten a sami jsou schopn si upct
					,teba suenky.
				</Text>
				<Gallery>
					<Img {...GALLERY['chapter-4-testo1']} />
					<Img {...GALLERY['chapter-4-testo2']} />
					<Img {...GALLERY['chapter-4-testo3']} />
				</Gallery>
			</Chapter>

			<Chapter {...metadata.activities[2]}>
				<Text>
					Je dleit, aby dti poznaly co nejvc druh povrch a to vetn
					teplot. Zmrazte jim do misky rzn drobn pedmty, pouijte i
					potravinsk barvivo a nechte je pozorovat a vytahovat hraky z ledu.
					Barvenm ledu tak rozvjte nejen hmat, ale i zrak.
				</Text>
				<Gallery>
					<Img {...GALLERY['chapter-4-led1']} />
					<Img {...GALLERY['chapter-4-led2']} />
				</Gallery>
			</Chapter>

			<Chapter {...metadata.activities[3]}>
				<Text>
					Nasypte si psek na podnos, nebo do krabice, pineste karte,
					hebeny, masn hebeny a nechte je pehrabovat se v psku. Pro dti,
					kter se potebuj seznmit se strukturou psku nensilnou metodou, je
					to skvl zatek. Pro dti, kter prv chtj nactit strukturu,
					pomcky nepotebuj, nechte je se prohrabovat holma rukama, pihodit
					mete i drobn pedmty. Jedl alternativa je krupice, anebo
					rozdrcen kukuin lupnky.
				</Text>
				<Gallery>
					<Img {...GALLERY['chapter-4-duny1']} />
					<Img {...GALLERY['chapter-4-duny2']} />
				</Gallery>
			</Chapter>

			<Chapter {...metadata.activities[4]}>
				<Text>
					Doma si mete vyrobit tak v vlastn kinetick psek, kter lze
					uchovvat ve vt plastov krabici. Lze z nj stavt a hrt si s
					pouitm mnoha rznch nstroj, tisknout do nj tvary, nebo jen tak
					mchat jako tsto. Je pevnj ne klasick psek a pro dti mnohdy
					zajmavj.
				</Text>
				<Text>
					Recep na vrobu psku mete nalzt{' '}
					<Link
						href="https://abecedazahrady.dama.cz/clanek/vyrobte-detem-vlastni-kineticky-pisek-je-to-jednoduche-a-usetrite-tady-jsou-3-vyzkousene-recepty"
						target="_blank"
						rel="noreferrer"
						underline
					>
						zde
					</Link>
					.
				</Text>
				<Gallery>
					<Img {...GALLERY['chapter-4-pisek1']} />
					<Img {...GALLERY['chapter-4-pisek2']} />
				</Gallery>
			</Chapter>

			<Chapter {...metadata.activities[5]}>
				<Text>
					Dtem, kterm krm na kui vad, doporuuju zat s natrnm nejprve
					vany, hraek nebo umyvadla, aby si na strukturu substance zvykly. A a
					pot mete zat od dlan nahoru k pam, ramenm a dolnm
					konetinm. Pro dti hyposenzitivn mete vyut peeling, nebo hust
					krmy a vy, nebo dt, roztrat krm celmi dlanmi.
				</Text>
			</Chapter>

			<Chapter {...metadata.activities[6]}>
				<Text>
					Koupn mete oivit pidnm pny (gelu) na holen, nebo mdlov
					pny. Nanet ji mete na tlo dtte, na stny, foukat do n (skvla
					motivace na procvien mimickho svalstva). Pnu mete vyut i mimo
					vanu, napklad v lavoru, pidat mete ndob a napodobovat tak jeho
					umvn. Pidnm barvy aktivitu oivte.
				</Text>
				<Gallery>
					<Img {...GALLERY['chapter-4-pena1']} />
					<Img {...GALLERY['chapter-4-pena2']} />
					<Img {...GALLERY['chapter-4-pena3']} />
				</Gallery>
			</Chapter>

			<Chapter {...metadata.activities[7]}>
				<Text>
					Pytlky meme naplnit rznm materilem - lutniny, mouka, cukr,
					re nebo kamnky. Jako obal meme vyut balnky, i ltkov
					pytlky. Hmatov pytlky meme vyut jako poznvn rznch
					struktur (ani by museli na n citlivj dti sahat), anebo jako
					pexeso, i piazovat jednotliv pytlky k obsahu. Dvajte vak pozor
					na prokousnut pytlku dtmi a pozen jeho obsahu.
				</Text>
				<Gallery>
					<Img {...GALLERY['chapter-4-pytliky1']} />
					<Img {...GALLERY['chapter-4-pytliky2']} />
				</Gallery>
			</Chapter>

			<Chapter {...metadata.activities[8]}>
				<Text>
					Balny jsou skvl vc pro dti, kter rdy makaj, dokou si tak
					krsn doplnit potebn senzorick vjemy. Navc zmknutm balonku
					pesn vid, jakou slu vynaloily (uiten u dt s problmy odhadu
					vlastn sly). Vyut mete balnky rznho proveden, hladk, s
					tsnmi nebo s gelovmi kulikami. Koupit se daji obyejn v
					obchodech jako je Kik, Nanu Nana nebo Tiger. Pokud m dt zvenou
					potebu kousat, zvolte sp ist zdravotn nezvadn balnky, pece
					jenom balnky z bnho obchodu nejsou uren ke kousn.
				</Text>
				<Gallery>
					<Img {...GALLERY['chapter-4-balony1']} />
				</Gallery>
			</Chapter>

			<Chapter {...metadata.activities[9]}>
				<Text>
					Balnky uren pmo na stimulaci taktilnho systmu v rznch
					provedench povrch. Vyut je mete nejen na makn v ruce, ale
					jako masn mky na cel tlo. Stimulace by mla bt jemn,
					nensiln, pokud si myslte, e vae dt vyaduje vt tlak, radji
					se porate s terapeutem. Dobr vrobky m firma Tullo.
				</Text>
				<Gallery>
					<Img {...GALLERY['chapter-4-balonky1']} />
					<Img {...GALLERY['chapter-4-balonky2']} />
				</Gallery>
			</Chapter>

			<Chapter {...metadata.activities[10]}>
				<Text>
					Co nejvc se snate o pohyb s dttem venku, pokud to jde, prochzejte
					se naboso, nebo s bearefoot botch. Je dleit, aby dti ctily
					pirozen tlak na pokoce nohou, stimuluje to nejen hmat, ale
					podporujete i rovnovhu chze. Prochzejte se na trv, kamnkch,
					hln, nechte dt voln bhat po zahrad (samozejm v rmci
					bezpenosti). Kdy mu poskytnete trochu volnosti, podporujte tm i
					zdravou sebedvru v sebe samho.
				</Text>
			</Chapter>

			<Chapter {...metadata.activities[11]}>
				<Text>
					Plastelna je vborn na makn, bouchn, vlen vlek. Mete
					stavt podle pedlohy, popisu nebo jen prost makat. Pokud nem dt
					rdo mastn pocit z klasick plastelny, anebo ji konzumuje,
					alternativou je playdough, kter je jedl. Je vyroben z kukuinho
					krobu a mete si ho vyrobit i sami.
				</Text>
				<Text>
					<Text as="span" fontStyle="italic">
						Nvod:
					</Text>{' '}
					Mouka - 1 hrnek, Sl - 1/2 hrnku, Voda- 1/2 hrnku, Potravinsk
					barvivo podle poteby. Ve zamchme a uskladujeme v uzavrateln
					ndob.
				</Text>
				<Text>
					<Text as="span" fontStyle="italic">
						Play foam
					</Text>{' '}
					je speciln hmota tvoena malmi polystyrenovmi kulikami a lepivou
					substanc. Je vhodn po hyposenzitivn dti, pro kter je samotn
					plastelina mlo citeln. Daj se z n tak vyrbt rzn vci, dti ji
					maj spe rdy na vlen kuliek a trhn.
				</Text>
				<Gallery>
					<Img {...GALLERY['chapter-4-foam1']} />
				</Gallery>
			</Chapter>

			<Chapter {...metadata.activities[12]}>
				<Text>
					Zpstupnte vodu dtti i mimo pravideln koupn a chozen na bazn.
					Napuste lavor pln vody, dejte tam rzn pedmty (kamnky, mule,
					t pedmty, knoflky, velk korle) a nechte je jm vylovit. Mou
					zapojit i nohy a prohrabvat se pedmty ve vod nokama. Pokud chceme
					zlepit taktiln diskriminaci, napuste dv ndoby, jednu s teplou a
					druhou se studenou vodou. Zante vdy s teplou vodou a nechte dt
					stdat teploty, jinmi teplotami si tak uvdom lepe jejich rozdl.
				</Text>
			</Chapter>

			<Chapter {...metadata.activities[13]}>
				<Text>
					Tzn. senzorick pytlk, je plastov prhledn pytel, do kterho se
					leje gel, nebo hust olej a rzn pedmty. Mete tam dt knoflky,
					kuliky, plastov psmena, perle a prsty pot posouvat jednotliv
					pedmty na pslun msto. Rozvj se tak krom hmatovho vnmn i
					jemn motorika. Mete pidat i hydrogenov perle (kdy jsou zaven v
					pytlku, nen jejich toxicita problm).
				</Text>
				<Gallery>
					<Img {...GALLERY['chapter-4-bag1']} />
					<Img {...GALLERY['chapter-4-bag2']} />
				</Gallery>
			</Chapter>

			<Chapter {...metadata.activities[14]}>
				<Text>
					K rozvoji hmatovho vnmn dolnch konetin je vhodn taktiln
					chodnk. D se bu koupit, anebo si ho mete vyrobit sami z
					prodnch materilu. S dtmi hyposenzitivnmi po nich chote boso,
					dti citlivj mohou mt zpotku ponoky na zmrnn tlaku.
				</Text>
				<Text>
					K dispozici jsou ploch taktiln disky, nebo plkulat nafoukl jeky.
					Ty jsou multifunkn a trnuj zrove i stabilitu (jdou obrtit
					naopak) a taktiln proitek je silnj.
				</Text>
				<Text>
					Vlastn vroba m vhodu, e mete povrch pizpsobit podle poteb
					dtte, navc me bt i levnj ne kupovan.
				</Text>
				<Gallery>
					<Img {...GALLERY['chapter-4-chodnik1']} />
					<Img {...GALLERY['chapter-4-chodnik2']} />
					<Img {...GALLERY['chapter-4-chodnik3']} />
				</Gallery>
			</Chapter>

			<Chapter {...metadata.activities[15]}>
				<Text>
					Pro dti s hyposenzitivnm vnmnm, je dobr jim taktiln zitek
					maximln zeslit. Proto jim meme picvaknout na koneky prst
					kolky na prdlo, vdy jeden po druhm a pot je nechat si s kolky
					hrt. Mohou s nimi klepat o sebe, nebo o nbytek, stahovat a smrovat
					prsty do dlan atd. Jde nm zejmna o zeslen hmatov zitek.
				</Text>
			</Chapter>

			<Chapter {...metadata.activities[16]}>
				<Text>
					Velmi dobr pomcka k rozvoji senzorick diskriminace hornch
					konetin. Mete si ji koupit, ale na trhu jsou zatm pouze velmi
					drah varianty, kter si ikovnej lid dok vyrobit sami. Na desku
					mete dt rzn materily, dleit je,aby byla textura rozdln.
					Pot nechme dt objevovat a zkoumat rzn textury.
				</Text>
				<Gallery>
					<Img {...GALLERY['chapter-4-tabule1']} />
					<Img {...GALLERY['chapter-4-tabule2']} />
				</Gallery>
			</Chapter>

			<Chapter {...metadata.activities[17]}>
				<Text>
					Vyut meme velk gymnastick m i men me. Kutlme vlee jemn
					po dolnch i hornch konetinch, nkde tlak meme zvit. Vdy se
					dme tm, jak je to dtti pjemn.
				</Text>
			</Chapter>
		</Chapter>
	</Chapter>
);

export default {
	...metadata,
	Content,
};
