import React, { FC } from 'react';

import Text from 'components/Text';
import Chapter from 'components/Chapter';
import { ListItem, UnorderedList } from 'components/List';

import { ChapterType } from 'pages/Chapters';

import { MapChapters } from 'utils';

import { ReactComponent as Nose } from 'assets/nose-front.svg';

const metadata = {
	id: 'cichove-vnimani',
	icon: Nose,
	title: 'ichov vnmn',
	color: '#ec8946',
	subchapters: MapChapters('cichove-vnimani', 'podkapitola', [
		'Hypersenzitivita',
		'Hyposenzitiva',
	]),
	activities: MapChapters('cichove-vnimani', 'aktivita', [
		'Esenciln oleje',
		'ichov pexeso',
	]),
} as ChapterType;

const Content: FC = () => (
	<Chapter variant="main" {...metadata}>
		<Chapter {...metadata.subchapters[0]}>
			<Text>Meme poznat dt podle toho, e:</Text>
			<UnorderedList>
				<ListItem>kad vn je pro nho pli intenzivn</ListItem>
				<ListItem>ve ct velmi extrmn, vetn odru osob</ListItem>
				<ListItem>
					nkte mvaj problmy s vyprazdovnm, ped pachy utkaj, nos
					stle stejn (sv) obleen
				</ListItem>
			</UnorderedList>
		</Chapter>

		<Chapter {...metadata.subchapters[1]}>
			<Text>Meme poznat tyto dti podle toho, e:</Text>
			<UnorderedList>
				<ListItem>
					u dt, kter vyhledvaj intenzivn pachy, jde nkdy a o kompulzivn
					oichvn
				</ListItem>
				<ListItem>
					dti ichaj k bnm vcem, jako jsou vlasy, knky, tuky
				</ListItem>
				<ListItem>
					nkdy si z toho udlaj i ritul a ne s vc manipuluj, mus ji
					nejdve oichat
				</ListItem>
			</UnorderedList>
		</Chapter>

		<Chapter variant="activity" {...metadata}>
			<Chapter {...metadata.activities[0]}>
				<Text>
					Aromaterapie je pro mnoho dti, ale i dosplch skvlm prostedkem k
					relaxaci, ale tak k aktivizaci smysl. Je poteba vyuvat kvalitn
					oleje, aby mly sprvn inek.
				</Text>
			</Chapter>

			<Chapter {...metadata.activities[1]}>
				<Text>Dt hd znm vn podle ichu, bez zrakov opory.</Text>
			</Chapter>
		</Chapter>
	</Chapter>
);

export default {
	...metadata,
	Content,
};
