import React, { FC } from 'react';

import Text from 'components/Text';
import Img from 'components/Img';
import { ListItem, NumberedList } from 'components/List';
import Chapter from 'components/Chapter';
import Gallery from 'components/Gallery';

import { ChapterType } from 'pages/Chapters';
import { GALLERY } from 'pages/Sources';

import { MapChapters } from 'utils';

import { ReactComponent as Brain } from 'assets/brain.svg';

const metadata = {
	id: 'senzoricka-integrace',
	icon: Brain,
	title: 'Senzorick integrace',
	color: '#ae688f',
	subchapters: MapChapters('senzoricka-integrace', 'podkapitola', [
		'Porucha smyslovho zpracovn',
		'Porucha smyslov modulace',
		'Porucha smyslov diskriminace',
		'Porucha na podklad motorickm',
	]),
	activities: [],
} as ChapterType;

console.log(GALLERY['chapter-1-sensor']);

const Content: FC = () => (
	<Chapter variant="main" {...metadata}>
		<Text>
			Senzorick, neboli smyslov integrace, je pirozen proces v lidskm
			mozku, za jej pomoc dokeme vnmat a dle zpracovvat vjemy okolo ns.
			Vsledek tto integrace je odpov naeho tla na dan vjem. Napklad,
			kdy se o nco splme, tak proces v naem mozku je takov, e detekuje
			ohe na na pokoce, do mozku proud informace, e ns pl pokoka a
			odpov tla je, e uhneme rukou. Tento sled infromac si sami o sob ani
			neuvdomme, ale proto, aby se dostavila dan reakce (uhnut ruky ped
			ohnm) potebujeme dobr fungovn vech st procesu.
		</Text>
		<Gallery>
			<Img {...GALLERY['chapter-1-sensor']} />
		</Gallery>
		<Text>
			K tomu, abychom dobe vnmali pchoz informace potebujeme smysly. Kad
			znme 5 smyslu - sluch, ich, chu, zrak a hmat. Nejsou to vak jedin,
			kter nm pomhaj registrovat svt okolo ns. K tmto pti musme jet
			pist vestibulrn vnmn a propriocepci.
		</Text>
		<Text>
			Sprvn senzorick zpracovn je dleit pro mnoho kadodennch innost,
			jako je pozornost, komunikace, pjem potravy, rovnovha, motorika aj.
			Kdy zjistme u nkoho obte v tchto oblastech, me to bt prve
			zapenn poruchou senzorick integrace. Pro je integrace smysl tak
			dleit, nm znzoruje tato pyramida:
		</Text>
		<Gallery>
			<Img {...GALLERY['chapter-1-pyramid']} />
		</Gallery>

		<Chapter {...metadata.subchapters[0]}>
			<Text>
				Musme si zpracovvn podnt z naeho okol pedstavit jako cestu, na
				kter se nachz nkolik zastvek. Kdy podnt zaregistrujeme, musme na
				nj umt adekvtn odpovdt a takt jednotliv vjemy od sebe rozliit.
			</Text>
			<Text>
				To, jak rzn vjemy meme zaregistrovat, rozdlujeme do tech
				kategori:
			</Text>
			<NumberedList>
				<ListItem>Porucha smyslov modulace</ListItem>
				<ListItem>Porucha smyslov diskriminace</ListItem>
				<ListItem>Motorick poruchy na senzorick bzi</ListItem>
			</NumberedList>
		</Chapter>

		<Chapter {...metadata.subchapters[1]}>
			<Text>
				Je to porucha sprvnho reagovn na smyslov podnty. Dti s touto
				poruchou maji obt adekvtn reagovat na pchoz vjem. Reakce me mt
				troj podobu.
			</Text>

			<Text fontWeight="bold" fontStyle="italic" mb={0}>
				Pehnan reakce na podnt (hypersenzitivita)
			</Text>
			<Text>
				Takov dti se budou odmtat dotknout nkterch povrch. Bude jim
				nepjemn dotkat se mastnho, slizkho, mokrho. Nepjemn jim bude
				sthn vlas, objet a nkter ltky. Me nastat i tzv. sumativn
				efekt. Jednotliv vjemy se bhem dne stdaj bez odpovdi a nhl
				pehnan reakce se objev na zdnliv nepodstatnm vjemu.
			</Text>

			<Text fontWeight="bold" fontStyle="italic" mb={0}>
				Nedostaten reakce na podnt (hyposenzitivita)
			</Text>
			<Text>
				Tento typ dt si vjemu nevimne, neodpov i reakce je opodn Tm, e
				podnty vnmaj nedostaten, stvaj se apatick, a letargick. Mal
				aktivita tedy nen zpsobena nedostatenou motivac, ale chybnm
				vnmnm podnt (dt se me zdt hluchm  nereaguje na hlasov
				podnty a ani na n patin neodpovd). Typick je i patn vnmn
				bolesti a teploty. asto jsou bhem kojeneckho obdob povaovny za
				hodn a rodie jejich pote rychle nerozpoznaj.
			</Text>

			<Text fontWeight="bold" fontStyle="italic" mb={0}>
				Aktivn vyhledvn podnt
			</Text>
			<Text>
				Jde o clen vyhledvn podnt dtmi, samy maj potebu se jimi
				dosytit, a tak vyhledvaj aktivity toho druhu, kter jsou jim pjemn
				a stimuluj je. Tento druh poruchy se nesousted na jeden senzorick
				systm, ale zashnout me jakkoli. Takov dti preferuji hlasitou
				hudbu, zvuky a zrak stimulujc objekty. Tm, e dt nezachycuje dn
				vjem, zane instinktivn podnty sbrat, nap. tepotnm ruikama,
				kolbnm i vydvnm zvuk.
			</Text>
		</Chapter>

		<Chapter {...metadata.subchapters[2]}>
			<Text>
				Tato porucha se projevuje v nedostatenm rozliovnm mezi podnty,
				jejich podobnost a intenzitou. Dti nedok rozliit mezi dvma
				rznmi vjemy, jejich trvnm a intenzitou. Sprvn funkce rozliovn
				mezi podnty je potebn pro plnovn pohyb, jazykov schopnosti,
				sluchov a zrakovm vnmn a v neposledn ad schopnost se bez
				problm uit. Obte u takovch dt se projevuj napklad jako
				neschopnost rozeznat rozdl v tvaru mezi gumou a kolem (bez pouit
				zraku), nedojde jim jak me plit svka i neodhadnou vku schod.
				Potebuj del as na zapnut zipu u bundy, ani by mli zrakovou
				kontrolu nad innost. Obte se mohou projevovat i v rozliovn
				tvarov a sluchov podobnch hlsek.
			</Text>
		</Chapter>

		<Chapter {...metadata.subchapters[3]}>
			<Text>
				U tto poruchy je hlavnm problmem rovnovha, pesun, nebo plnovn
				srie pohyb v reakci na smyslov poadavky. U dtte se objevuj
				problmy s koordinac a kontrolou vlastnho tla.
			</Text>
			<Text>
				Dlme je na dv podkategorie:{' '}
				<Text as="span" fontStyle="italic">
					Dyspraxie a posturln porucha
				</Text>
			</Text>

			<Text fontWeight="bold" fontStyle="italic" mb={0}>
				Posturln porucha
			</Text>
			<Text>
				Projevy u takovch dt jsou v zhoren stabilit tla, neadekvtnm
				svalovm naptm a patnou funkc sval pi pohybu proti gravitaci (smr
				vzhru, dt m slab svalov napt). Dti maj problm odhadnout jak
				moc je od nich pedmt vzdlen, nevydr dlouho v jedn poloze.
				Projevit se to me pi psan  potebuj si podloit hlavu rukami, nebo
				si lehnout na lavici. Jsou asto unaven, vypadaj jako kdyby byly
				ochabl.
			</Text>

			<Text fontWeight="bold" fontStyle="italic" mb={0}>
				Dyspraxie
			</Text>
			<Text>
				Dti s dyspraxii maj obte se zatkem, plnovnm, organizovnm a
				provdn sekvenc pohyb. Zasahuje do interakce s fyzickm prostedm,
				hrub a jemn motorika, spolen s oromotorikou psob nekoordinovan a
				neohraban. Dyspraxie se asto poj s poruchou senzorick registrace a
				diskriminace. Vykonvn neznmch aktivit je pro n obtn. Bn
				aktivity jsou pro n skliujc  oblkn, nalvn mlka, nastupovn
				do autobusu i jenom strouhn tuky. Maj problm odhadnout smr
				(maminka ekne b dozadu, dt zane utkat dopedu, nebo jin smrem),
				pohyby podle instrukc jsou pro n velmi obtn.
			</Text>

			<Text fontWeight="bold" fontStyle="italic" mb={0}>
				Poznmka
			</Text>
			<Text>
				U dt s PAS je vskyt pot se zpracovnm nkterch smyslovch vjem
				velmi ast. Nemus se vak nutn rovnou jednat o poruchu senzorickho
				zpracovn. Tuto poruchu me diagnostikovat pouze prokolen odbornk
				(ergoterapeut, klinick logoped, fyzioterapeut, speciln pedagog),
				proto je poteba se s nkm takovm poradit, jet ped zatkem
				jakkoli intenzivnj prce s prvky senzorick integrace!
			</Text>
		</Chapter>
	</Chapter>
);

export default {
	...metadata,
	Content,
};
