import React, { FC } from 'react';

import Text from 'components/Text';
import Chapter from 'components/Chapter';
import { ListItem, UnorderedList } from 'components/List';

import { ChapterType } from 'pages/Chapters';

import { MapChapters } from 'utils';

import { ReactComponent as Tongue } from 'assets/tongue.svg';

const metadata = {
	id: 'chutove-vnimani',
	icon: Tongue,
	title: 'Chuov vnmn',
	color: '#d3746a',
	subchapters: MapChapters('chutove-vnimani', 'podkapitola', [
		'Hypersenzitivita',
		'Hyposenzitiva',
	]),
	activities: MapChapters('chutove-vnimani', 'aktivita', [
		'Zvyovn tolerance struktur v stech',
		'Ochutnvn novch chut',
		'Kupav dlo',
		'Koustka',
	]),
} as ChapterType;

const Content: FC = () => (
	<Chapter variant="main" {...metadata}>
		<Chapter {...metadata.subchapters[0]}>
			<Text>Meme poznat dt podle toho, e:</Text>
			<UnorderedList>
				<ListItem>kad chu moc ostr, nebo sladk</ListItem>
				<ListItem>
					v jdle jsou velmi vybrav a j velmi mlo - obas se me dostat a
					k porue pjmu potravy
				</ListItem>
				<ListItem>
					maj posunut dviv reflex, v souvislosti s jdlem asto zvrac, nebo
					se dv
				</ListItem>
				<ListItem>ast je rigidita v druzch jdel</ListItem>
				<ListItem>
					maj pote s rznou texturou jdel, odmtaj nov chut, nkdy m
					pote i s barvou jidel
				</ListItem>
				<ListItem>vyhledv rozmixovan jdlo i ve starm vku</ListItem>
			</UnorderedList>
		</Chapter>

		<Chapter {...metadata.subchapters[1]}>
			<Text>Meme poznat dt podle toho, e:</Text>
			<UnorderedList>
				<ListItem>
					takov dt je schopno snst cokoliv (tzv. pica syndrome) j sklo,
					psek, devo aj.
				</ListItem>
				<ListItem>
					asto objevuje svt pomoc pusy, ve olizuj, strkaj do st
				</ListItem>
				<ListItem>
					vyhovuj jim kombinace rznch chut (kysel a sladk)
				</ListItem>
				<ListItem>sousto vrac zptky do st nkolikrt</ListItem>
			</UnorderedList>
		</Chapter>

		<Chapter variant="activity" {...metadata}>
			<Chapter {...metadata.activities[0]}>
				<Text>
					U dt hypersenzitivnch potebujeme zvit odolnost v dutin stn.
					Nikdy vak nezanme pmo v stech, ale na tle. Nejprve provdme
					tlakov mase po tle, zat od nejmn citlivho msta na a
					pokraujeme a k dlanm. Vyut meme tlak vlastnch rukou, nebo
					vibrace z rotavibrtoru. Pokud dt zvldne tlak na dlanch, meme se
					pomalu pesunout k tvm, elisti a a pot rtm a dutin stn!
				</Text>
			</Chapter>

			<Chapter {...metadata.activities[1]}>
				<Text>
					Vyut meme nanuky vyroben z rznch druh sirupu, nebo zmraenho
					ovoce. Dt nechme jejich vlastnm tempem ochutnat, osahat, oichat
					ve co jsme mu nachystali.
				</Text>
			</Chapter>

			<Chapter {...metadata.activities[2]}>
				<Text>
					Podvejte jdlo, kter mus dt vkat. Mrkev, kupky, kousky ovoce
					(mete ho vytvarovat do zbavnho tvaru) vkaky. Vbr jdla ite
					podle dietnch restrikc, kter dt m a podle rovn oromotorickch
					schopnost.
				</Text>
			</Chapter>

			<Chapter {...metadata.activities[3]}>
				<Text>
					Koustek je na trhu spousty, ped jejich koup se ale urit porate s
					odbornkem (logoped, poradkny v obchodu s koustky). Koustka jsou
					vhodn pro potlaen silnho tonusu v tle, odnauen cucn prst,
					zven tolerance pedmt v dutin stn. Nebo naopak pro dti, kter
					kousat potebuj a kouou rzn nejedl pedmty (sklo, devo,
					obleen, kameny). Koustka jsou rozdlen do t druh, prv podle
					specifick poteby.
				</Text>
			</Chapter>
		</Chapter>
	</Chapter>
);

export default {
	...metadata,
	Content,
};
