import React, { FC } from 'react';

import Text from 'components/Text';
import Chapter from 'components/Chapter';
import { ListItem, UnorderedList } from 'components/List';

import { ChapterType } from 'pages/Chapters';

import { MapChapters } from 'utils';

import { ReactComponent as Ear } from 'assets/human-ear.svg';

const metadata = {
	id: 'sluchove-vnimani',
	icon: Ear,
	title: 'Sluchov vnmn',
	color: '#999543',
	subchapters: MapChapters('sluchove-vnimani', 'podkapitola', [
		'Hypersenzitivita',
		'Hyposenzitiva',
	]),
	activities: MapChapters('sluchove-vnimani', 'aktivita', [
		'Hra na hudebn nstroje',
		'Poslech zvuk zvat v okol, nebo v nahrvky',
		'Terapeutick poslech',
		'EASe CD',
		'Sluchov pexeso',
		'Poslech Mozartovy hudby',
		'Sluchtka izolujc vnj zvuky',
	]),
} as ChapterType;

const Content: FC = () => (
	<Chapter variant="main" {...metadata}>
		<Chapter {...metadata.subchapters[0]}>
			<Text>Meme poznat tyto dti podle toho, e:</Text>
			<UnorderedList>
				<ListItem>tzv. slyen neslyitelnho</ListItem>
				<ListItem>
					zvuky maj zeslen nkdy tak moc, e sly i frekvence, slyiteln
					pouze zvatm
				</ListItem>
				<ListItem>
					asto se tak radji stran rozhovor, jeliko jim pinej velk
					diskomfort
				</ListItem>
				<ListItem>
					velmi asto si zakrvaj ui, boj se vc, je vyluzuj hlasit zvuky
					(bouka, davy, MHD)
				</ListItem>
				<ListItem>
					Ve snaze pehluit vnj zvukov podnty sami vydvaj stereotypn
					zvuky
				</ListItem>
			</UnorderedList>
		</Chapter>

		<Chapter {...metadata.subchapters[1]}>
			<Text>Meme poznat dt podle toho, e:</Text>
			<UnorderedList>
				<ListItem>
					zmrn vyhledv zvuky, tiskne si ui k rdiu, tabletu, nebo televizi
				</ListItem>
				<ListItem>libuje si ve zvuku sirn a hlasit muzice</ListItem>
				<ListItem>
					asto se sm sna sluch stimulovat  vokalizuje, bouch dvemi, uk
					do pedmt
				</ListItem>
				<ListItem>dl mu dobe doprava, davy, tak m rd vibrace</ListItem>
				<ListItem>
					m pote s porozumnm, sluchovou pamt, gramatikou a rytmikou
				</ListItem>
				<ListItem>
					mv obte v oblast artikulace a samotn funkn komunikaci
				</ListItem>
				<ListItem>
					komunikan schopnost se lep po zkuenosti s intenzivnm pohybem
				</ListItem>
			</UnorderedList>
		</Chapter>

		<Chapter variant="activity" {...metadata}>
			<Chapter {...metadata.activities[0]}>
				<Text>
					Vhodn je nap. xylofon, u kterho mete pracovat i se zrakovou a
					hudebn pamt. Mete vytvoit noty podle barviek klaves xylofonu.
					Nauit se jednoduch psniky, nebo jenom tak nechat dt opakovat
					zvuky. Vhodn jsou tak bubnky (ne elektrick), kde meme spojit i
					regulaci sly a procvien rytmiky. Vytvoit si mete i vlastni
					zvonkohru z pbor a vaeky, opt zde meme spojit regulaci sly v
					hodu, jemnou motoriku a koordinaci oko - ruka.
				</Text>
				<Text>
					Takt mete vyut dechov nstroje (fltny, foukac harmonika )
					upevuje svalstvo st a sprvn vdechov proud.
				</Text>
			</Chapter>

			<Chapter {...metadata.activities[1]}>
				<Text>
					Mete zvuky rozeznvat, urovat komu pat a nsledn si o zvtkch
					povdat.
				</Text>
			</Chapter>

			<Chapter {...metadata.activities[2]}>
				<Text>
					Pro dt s dysfunkc nejen ve sluchovm vnmn, ale ve vech
					smyslech, se vyuv terapeutickho poslechu modulovan hudby dle
					Tomatise, Berarda a Steinbacha (zde je dleit dodret, aby terapii
					provdl prokolen terapeut). Hudba je elektronicky upravena, tak by
					vyvolala specifick odpovdi a pomh s tvorbou adaptivn odpovdi ve
					smyslovm zpracovn.
				</Text>
			</Chapter>

			<Chapter {...metadata.activities[3]}>
				<Text>
					Jsou zaloen tak na programu sluchov stimulace. Ta je vytvoena
					kdovnm hudby v rmci parametr Berardovy sluchov integrace.
					Princip poslechu je velmi podobn, 2x denn po dobu 30 min. Vhodou
					je, e nen poteba terapeuta, ale CD jsou pedem nechystny.
				</Text>
			</Chapter>

			<Chapter {...metadata.activities[4]}>
				<Text>
					Je mon ho zakoupit, ale tak vyrobit sami. Napklad z krabiek od
					sirek, nebo lahviek od lk. Do nich vlome rzn materily
					(hebky, sul, koen, lutniny). Ndoby vyrobme do pru a meme
					bud hrt pexeso, nebo loto i hdn osahu ndoby.
				</Text>
			</Chapter>

			<Chapter {...metadata.activities[5]} />

			<Chapter {...metadata.activities[6]}>
				<Text>
					Speciln sluchtka, vhodn pro dti hypersenzitivn na zvuky.
					Poskytnou jim tam chvli klidu a odpoinku, dobr vyuvat v hlunm
					prosted, nebo ped spanm.
				</Text>
			</Chapter>
		</Chapter>
	</Chapter>
);

export default {
	...metadata,
	Content,
};
