import React, { FC } from 'react';

import Text from 'components/Text';
import Chapter from 'components/Chapter';
import Img from 'components/Img';
import Gallery from 'components/Gallery';
import { ListItem, UnorderedList } from 'components/List';

import { ChapterType } from 'pages/Chapters';
import { GALLERY } from 'pages/Sources';

import { MapChapters } from 'utils';

import { ReactComponent as Eye } from 'assets/semi-closed-eye.svg';

const metadata = {
	id: 'zrakove-vnimani',
	icon: Eye,
	title: 'Zrakov vnmn',
	color: '#567a59',
	subchapters: MapChapters('zrakove-vnimani', 'podkapitola', [
		'Hypersenzitivita',
		'Hyposenzitiva',
		'Vyhledvaj extra podnty',
	]),
	activities: MapChapters('zrakove-vnimani', 'aktivita', [
		'Svteln bublinkov vlec',
		'Lvov lampa',
		'Svteln pedmty',
		'Svteln krabice',
		'Hra na rybe',
		'Rozpoznvn pedmt',
		'Orientace v prosted',
	]),
} as ChapterType;

const Content: FC = () => (
	<Chapter variant="main" {...metadata}>
		<Chapter {...metadata.subchapters[0]}>
			<Text>Meme poznat dt podle toho, e:</Text>
			<UnorderedList>
				<ListItem>
					vad mu velmi ostr svtlo (zakrv si oi) a zrove tma
				</ListItem>
				<ListItem>zalekne se ostrch zblesk (blesky)</ListItem>
				<ListItem>asto skln zrak do zem</ListItem>
				<ListItem>
					neustle sleduje miniaturn steky, bere do ruky malik prachov
					smtka
				</ListItem>
				<ListItem>rovn si asto stuje na dvojit vidn</ListItem>
				<ListItem>m obte s pesunem zraku z objektu na objekt</ListItem>
				<ListItem>
					ct se zahlcen, kdy se okolo nj pohybuje moc lid, nebo vc a je
					velmi brzy z toho unaven
				</ListItem>
			</UnorderedList>
		</Chapter>

		<Chapter {...metadata.subchapters[1]}>
			<Text>Meme poznat dt podle toho, e:</Text>
			<UnorderedList>
				<ListItem>
					vid pouze obrysy, i to nejostej svtlo je pro nj slab
				</ListItem>
				<ListItem>mnohdy zr pmo slunce, bez pocitu bolesti</ListItem>
				<ListItem>
					pedmty obchz, ale zrove se jich dotk prsty, aby poznal i jinm
					smyslem, co je okolo nho
				</ListItem>
				<ListItem>
					takt se nesprvn orientuje v prostoru, nar do vc, nbytku a
					rychle se pohybujc pedmty nedoke zaznamenat (tenisov mek)
				</ListItem>
				<ListItem>
					pi prci s jemnou motorikou mu dlj pote kony zahrnujcch
					prostorov vztahy  nap. dvn tvar do vkldaky
				</ListItem>
				<ListItem>patn se orientuje v textu, pe skopce</ListItem>
			</UnorderedList>
		</Chapter>

		<Chapter {...metadata.subchapters[2]}>
			<Text>Meme poznat dt podle toho, e:</Text>
			<UnorderedList>
				<ListItem>
					fascinuj ho jasn svteln jevy  odlesky, blikajc svtlka, lvov
					lampy, titulky bc na televizi
				</ListItem>
				<ListItem>
					ve potebuje vidt velmi zblzka  tiskne se k okrajm prostoru
				</ListItem>
				<ListItem>pejd po hranch pedmt</ListItem>
				<ListItem>rychle si kmit prsty ped oima</ListItem>
				<ListItem>asto sleduje vci koutkem oka a ne napmo</ListItem>
			</UnorderedList>
		</Chapter>

		<Chapter variant="activity" {...metadata}>
			<Chapter {...metadata.activities[0]}>
				<Text>
					Vlec produkuje barevn pjemn svtlo, vn mohou plavat rybiky,
					nebo bublinky. Je dobrm pomocnkem k ncviku udren pozornosti,
					pojmenovn barev, relaxaci. Vhodn pro dti nestlou emocionalitou,
					krsn se dok u svtel zklidnit.
				</Text>
				<Gallery>
					<Img {...GALLERY['chapter-5-valec1']} />
				</Gallery>
			</Chapter>

			<Chapter {...metadata.activities[1]}>
				<Text>
					Pomcka, kterou si mete vyrobit doma. Sta nasypat trochu sody na
					dn lhve, pidat do dvou tetin lhve olej a pot obarevn ocet. Soda
					zane reagovat s octem v oleji do bublinek. Ocet takt meme
					pidvat do lhve po trokch kaptkem a trnovat tak jemnou motoriku.
					Pro dal aktivity na stimulaci zrakovho vnmn mete vyut
					sklenici naplnnou vodou a holc pnou, do kter kaptkem kapete
					potravinsk barvivo.
				</Text>
				<Text>
					Alternativa ke koupi jsou tyto pespac tekut hodiny (tyto jsou
					koupen na strnce kidtown).
				</Text>
				<Gallery>
					<Img {...GALLERY['chapter-5-lampa1']} />
					<Img {...GALLERY['chapter-5-lampa2']} />
					<Img {...GALLERY['chapter-5-lampa3']} />
				</Gallery>
			</Chapter>

			<Chapter {...metadata.activities[2]}>
				<Text>
					Vyut mete baterky, svteln etzy, svteln me, obrzkov
					kotoue nebo kouzeln lampy (svteln koule co produkuj bu
					relaxan svtlo, nebo naopak svteln zblesky, podle poteb dtte
					viz. fot). Jde nm zejmna o relaxan el, svtlo poutjte dtem v
					temn mstnosti, nap pod dekou, v bunkru, nebo v uzaven mstnosti.
				</Text>
				<Gallery>
					<Img {...GALLERY['chapter-5-predmety1']} />
					<Img {...GALLERY['chapter-5-predmety2']} />
				</Gallery>
			</Chapter>

			<Chapter {...metadata.activities[3]}>
				<Text>
					Krabice je zespodu podsvcen, tak aby zvraznila co nejvce pedmty
					na n poloen. Dobr je v mstnosti zhasnout a nechat svtit pouze
					krabici, dtem se tak sn monost rozruovat se jinmi podnty a
					mohou se soustedit pouze na koly na krabici. Na vko krabice mete
					vkldat barevn prsvitn obrazce (viz spodn obrzek), nebo nasypat
					mouku a kreslit sla, nebo psmena.
				</Text>
				<Gallery>
					<Img {...GALLERY['chapter-5-krabice1']} />
					<Img {...GALLERY['chapter-5-krabice2']} />
				</Gallery>
			</Chapter>

			<Chapter {...metadata.activities[4]}>
				<Text>
					Mete si sami z kousku vtviky, provzku a magnetu vyrobit prut.
					Sta jenom nachystat paprov, nebo ltkov rybiky s magnety. Clem
					je pln zrakov kontrola a motoricka koordinace, aby dt rybiku
					chytilo.
				</Text>
			</Chapter>

			<Chapter {...metadata.activities[5]}>
				<Text>
					V ppad, e m dt problm rozpoznat pedmt, pokud ho nem
					zobrazen cel, nebo nerozpozn rozdly mezi pedmty a tvary,
					nerozpozn oblieje. Mete zkusit tyto hry:
				</Text>
				<UnorderedList>
					<ListItem>
						piazovn stejnch dvojic k sob (boty, rzn prodniny, ponoky)
					</ListItem>
					<ListItem>skldn zjednoduenho (3-5 dlnho) puzzle</ListItem>
					<ListItem>
						piazovn grafickch objekt k sob (3D na 2D/ stejn obrzky s
						rznou barvou)
					</ListItem>
					<ListItem>hledn vchodu v paprovm bluditi</ListItem>
					<ListItem>pexeso</ListItem>
				</UnorderedList>
			</Chapter>

			<Chapter {...metadata.activities[6]}>
				<Text>
					Navigujeme dt ve znmm prosted, ono mezitm ukld vci na msto,
					dle pokynu. Procviujete sluchovou pamt, vnmn a diferenciaci
					hlsek.
				</Text>
			</Chapter>
		</Chapter>
	</Chapter>
);

export default {
	...metadata,
	Content,
};
